@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

@media (max-width: 1023px) {
  .container {
    max-width: 1020px;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 669px;
    padding: 0 26px 0 26px;
  }
}
.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.btn--callback {
  background-color: transparent;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
}

.btn--form-call {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
  background-color: #00b2ff;
  border-radius: 3px;
  padding: 15px 25px;
}
@media (max-width: 767px) {
  .btn--form-call {
    font-size: 14px;
    line-height: 16px;
    padding: 10px 30px;
    width: 100%;
    display: flex;
    justify-content: center;
  }
}

.btn--about-as {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #00b2ff;
  padding: 14px 13px 8px 12px;
  background-color: transparent;
  border: 1px solid #00b2ff;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  width: 180px;
  min-height: 45px;
  margin-top: 20px;
}

.btn--product {
  width: 60px;
  height: 60px;
  background-color: #00b2ff;
  border-radius: 3px 0 0 0;
}
@media (max-width: 767px) {
  .btn--product {
    width: 40px;
    height: 40px;
  }
}

.btn--product::before {
  content: "";
  width: 5px;
  height: 10px;
  background-image: url("../img/svg/arrow-right-icon.svg");
  position: absolute;
  top: 25px;
  left: 29px;
}
@media (max-width: 767px) {
  .btn--product::before {
    top: 15px;
    left: 19px;
  }
}

.btn--form-submit {
  background-color: #041427;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  display: block;
  width: 265px;
  min-height: 45px;
}
@media (max-width: 767px) {
  .btn--form-submit {
    width: 100%;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: relative;
  z-index: 2;
  margin-bottom: -19px;
}

@media (min-width: 1024px) {
  .main-header--sticky {
    position: sticky;
    width: 100%;
    top: 0;
  }
}
.main-navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0 35px 0;
}

.main-navigation__link {
  margin-right: 30px;
}

@media (max-width: 1023px) {
  .main-navigation {
    padding: 20px 0 25px 0;
  }
  .main-navigation__callback-button {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-navigation {
    padding: 10px 0 20px 0;
  }
  .main-navigation__callback-button {
    display: none;
  }
}
.logo-link__icon--short {
  display: none;
}

@media (max-width: 767px) {
  .logo-link__icon--full {
    display: none;
  }
  .logo-link__icon--short {
    display: block;
  }
}
.navigation-info {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 480px;
}

.navigation-info__item {
  width: 225px;
}

.navigation-info__text {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
}

.navigation-info__link {
  padding: 10px 0 10px 35px;
  background-position: left center;
  background-repeat: no-repeat;
}

.navigation-info__link--mail {
  background-image: url("../img/svg/email-icon.svg");
}

.navigation-info__link--tell {
  background-image: url("../img/svg/phone-call-icon.svg");
}

@media (max-width: 1023px) {
  .navigation-info__item {
    width: 40px;
  }
  .navigation-info__item:not(:last-child) {
    margin-right: 20px;
  }
  .navigation-info__link {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    background-position: center center;
    width: 40px;
    height: 40px;
  }
  .navigation-info__text {
    display: none;
  }
  .navigation-info {
    width: min-content;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    margin-left: auto;
  }
}
@media (max-width: 767px) and (min-width: 320px) {
  .navigation-info__text {
    display: none;
  }
  .navigation-info {
    width: min-content;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-left: auto;
  }
}
.main-footer {
  background-color: #041827;
  padding: 50px 0 55px 0;
}

.main-footer__logo-link {
  display: block;
  margin-bottom: 30px;
}

.main-footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 15px;
  width: 205px;
  margin-left: 15px;
}

.main-footer__categories-title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;
  margin-top: 10px;
}

.main-footer__categories-title-wrapper {
  margin: 0;
  margin-bottom: 20px;
}

.main-footer__categories-list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 320px;
}

.main-footer__categories-list--navigation {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 140px;
  width: 375px;
}

.main-footer__categories-item:not(:last-child) {
  margin-bottom: 5px;
}

.main-footer__categories-link,
.main-footer__address {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
  display: inline-block;
}

.main-footer__categories-button {
  display: none;
}

@media (max-width: 1023px) {
  .main-footer__categories-list--navigation {
    height: max-content;
  }
  .main-footer__social-logo-wrapper {
    grid-column: 1/-1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 35px;
    margin-right: 0;
  }
  .main-footer__logo-link {
    display: block;
    margin-bottom: 0;
  }
  .main-footer__social-list {
    margin-left: 0;
    width: max-content;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 767px) {
  .main-footer {
    background-color: #041827;
    padding: 35px 0 35px 0;
  }
  .main-footer__social-logo-wrapper {
    width: 215px;
    flex-direction: column;
    align-items: center;
  }
  .main-footer__categories-title {
    margin-top: 0;
  }
  .main-footer__categories-list {
    display: none;
    width: 100%;
    margin-top: 15px;
  }
  .main-footer__categories-list--navigation {
    width: 100%;
  }
  .main-footer__categories-wrapper {
    width: 100%;
    max-width: 767px;
    padding: 15px 25px 15px 25px;
    border-top: 2px solid rgba(248, 253, 255, 0.7);
    border-bottom: 2px solid rgba(248, 253, 255, 0.7);
  }
  .main-footer__categories-wrapper:nth-child(even) {
    border-bottom: none;
  }
  .main-footer__categories-title-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0;
  }
  .main-footer__categories-button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border: none;
    background-color: transparent;
    padding: 0;
  }
  .main-footer__categories-button::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 4px;
    width: 16px;
    height: 3px;
    background-color: #c4c4c4;
  }
  .main-footer__categories-button::after {
    content: "";
    position: absolute;
    top: 3px;
    left: 11px;
    width: 3px;
    height: 16px;
    background-color: #c4c4c4;
  }
  .main-footer__categories-list--opened {
    display: block;
  }
  .main-footer__categories-button--opened::after {
    display: none;
  }
}
