.feedback-form {
  margin-left: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 30px;
  row-gap: 20px;
  width: 580px;
}
.feedback-form h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
  margin: 0;
  grid-column: 1/-1;
}
.feedback-form p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  grid-column: 1/-1;
  grid-row: 2/3;
  margin: 0;
}

.feedback-form__field input {
  width: 275px;
  height: 50px;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  padding: 15px;
}
.feedback-form__field input::placeholder {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.feedback-form__field--submit {
  grid-column: 1/2;
  grid-row: 5/6;
}

.feedback-form__field--approval {
  grid-column: 2/3;
  grid-row: 5/6;
  padding-left: 30px;
}
.feedback-form__field--approval label {
  position: relative;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  cursor: pointer;
}
.feedback-form__field--approval label::before {
  content: "";
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  position: absolute;
  top: 11px;
  left: -30px;
  cursor: pointer;
}
.feedback-form__field--approval input:checked + label::before {
  background-image: url("../img/svg/approval-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.feedback-form__field--question {
  grid-column: 1/-1;
}
.feedback-form__field--question textarea {
  width: 100%;
  height: 185px;
  resize: none;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 15px;
  color: #ffffff;
  border: none;
}
.feedback-form__field--question textarea::placeholder {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.feedback-form--modal {
  position: relative;
  margin-left: 0;
  background-color: #00b3ff;
  border-radius: 3px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  width: 500px;
}
.feedback-form--modal h2 {
  font-size: 30px;
  line-height: 42px;
}
.feedback-form--modal p {
  font-size: 16px;
  line-height: 22px;
}
.feedback-form--modal input {
  width: 420px;
}
.feedback-form--modal .feedback-form__field--approval {
  order: 2;
}
.feedback-form--modal .feedback-form__field--approval label::before {
  top: 0;
  left: -28px;
}
.feedback-form--modal .feedback-form__button-submit {
  width: 420px;
}
.feedback-form--modal .feedback-form__button-close {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 15px;
  right: 15px;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}
.feedback-form--modal .feedback-form__close-icon {
  color: #ffffff;
}

.feedback-form--open {
  position: fixed;
}

@media (max-width: 1023px) {
  .feedback-form {
    margin: 0 auto;
    width: 670px;
    max-width: 800px;
  }
  .feedback-form__field input {
    width: 100%;
  }
  .feedback-form__field--approval {
    width: 235px;
  }
  .feedback-form__field--approval input {
    width: 30px;
  }
}
@media (max-width: 767px) {
  .feedback-form {
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 100%;
    max-width: 630px;
  }
  .feedback-form h2 {
    font-size: 30px;
    line-height: 42px;
  }
  .feedback-form__field {
    width: 100%;
  }
  .feedback-form__field--approval {
    width: 230px;
    order: 2;
  }
  .feedback-form__field--approval label::before {
    top: 10px;
    left: -30px;
  }
}
.modal-feedback {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(3, 16, 27, 0.7);
  z-index: 2;
}

.modal-feedback--close {
  display: none;
}

