.btn {
  display: inline-flex;
  padding: 10px 20px;

  color: $color-default-white;

  background-color: $color-neon-blue;
  border: none;
  cursor: pointer;

  transition: opacity $trans-default;

  &--red {
    background-color: $color-torch-red;
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }
}

.btn--callback {
  background-color: transparent;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
}

.btn--form-call {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
  background-color: #00b2ff;
  border-radius: 3px;
  padding: 15px 25px;

  @media (max-width: 767px) {
    font-size: 14px;
    line-height: 16px;
    padding: 10px 30px;
    width: 100%;
    display: flex;
    justify-content: center;
  }
}

.btn--about-as {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #00b2ff;
  padding: 14px 13px 8px 12px;
  background-color: transparent;
  border: 1px solid #00b2ff;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  width: 180px;
  min-height: 45px;
  margin-top: 20px;
}

.btn--product {
  width: 60px;
  height: 60px;
  background-color: #00b2ff;
  border-radius: 3px 0 0 0;

  @media (max-width: 767px) {
    width: 40px;
    height: 40px;
  }
}

.btn--product::before {
  content: "";
  width: 5px;
  height: 10px;
  background-image: url("../img/svg/arrow-right-icon.svg");
  position: absolute;
  top: 25px;
  left: 29px;

  @media (max-width: 767px) {
    top: 15px;
    left: 19px;
  }
}

.btn--form-submit {
  background-color: #041427;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  display: block;
  width: 265px;
  min-height: 45px;

  @media (max-width: 767px) {
    width: 100%;
  }
}
